<template>
    <div class="reservation-info" v-loading="loading"  v-if="listData">
        <div class="content-top">
            <p class="tips">{{ listData.statusDetail }}</p>

        </div>
        <div class="order-details">
            <p class="title">{{ $L('订单信息') }}</p>
            <ul class="order-ul">
                <li class="order-li" v-for="item in listData.fromDetail">
                    <span>{{ item }}</span>
                </li>
            </ul>
        </div>
        <div class="prod-info" style="display: none">
            <el-table
                    :data="listData.goodsArr"
                    :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666'}"
                    style="width: 100%;display: none;" class="table">
                <el-table-column
                        :label="$L('产品信息')"
                        align="left"
                        min-width="200">
                    <div slot-scope="scope" class="info">
                        <div class="list-main-left">
                            <img class="img" :src="scope.row.pic" alt="">
                        </div>
                        <div class="list-main-right">
                            <p class="name">{{scope.row.goodsName}}</p>
                            <p class="name" v-html="scope.row.goodsSpec"></p>
                        </div>
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('价格')"
                        align="center">
                    <div slot-scope="scope">
                        {{ scope.row.goodsPrice }}
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('类型')"
                        align="center">
                    <div slot-scope="scope">
                        {{ scope.row.type }}<br>
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('状态')"
                        align="center">
                    <div slot-scope="scope" class="status">
                        <div v-if="listData.order_status === 1" class="font font-red">{{$L('待处理')}}</div>
                        <div v-else-if="listData.order_status === 2" class="font">{{$L('已消费')}}</div>
                        <div v-else-if="listData.order_status === 3" class="font">{{$L('已取消预约')}}</div>
                        <div v-else-if="listData.order_status === 4 && listData.commentStatus === 0" class="font">{{$L('待评价')}}</div>
                        <div v-else-if="listData.order_status === 4 && listData.commentStatus === 1" class="font">{{$L('已评价')}}</div>
                        <div v-else-if="listData.order_status === 5" class="font">{{$L('已逾期')}}</div>
                        <div v-else class="font">{{$L('未知状态')}}</div>
                    </div>
                </el-table-column>
            </el-table>
        </div>
        <div class="prod-info" v-for="item in listData.goodsArr">
            <div class="info">
                <div class="list-main-left">
                    <img class="img" :src="item.pic" alt="">
                </div>
                <div class="list-main-right">
                    <p class="name">{{ item.goodsName }}</p>
                    <p class="name" v-html="item.goodsSpec"></p>
                </div>
            </div>
            <div class="price">
                {{ item.goodsPrice }}
            </div>
        </div>
        <div class="prod-bottom">
            <div class="right-box">
                <ul class="ul">
                    <li class="li"><p>{{ $L('订单总额') }}:</p><p>{{ listData.orderAmount }}</p></li>
                    <li class="li"><p>{{ $L('店铺优惠') }}:</p><p>{{ listData.discount }}</p></li>
                    <li class="li"><p>{{ $L('到店支付') }}:</p><p>{{ listData.prepaidAmount }}</p></li>
                    <li class="li"><p class="font-black">{{ $L('预付金额') }}:</p><p class="font-red">{{ listData.moneyPaid }}</p></li>
                </ul>
            </div>
        </div>
        <div class="prod-msg">
            <p class="order-sn">{{ $L('订单编号') }}：{{ listData.orderSn }}</p>
            <p class="order-sn">{{ $L('下单时间') }}：{{ listData.addTime }}</p>
        </div>
        <div class="bottom-btns">
            <el-button type="primary" size="small" v-if="listData.order_status === 1 && listData.pay_status === 0" @click="changeOrderType(listData,'pay')">{{$L('立即支付')}}</el-button>
            <el-button type="default" size="small" v-if="listData.order_status === 1"  @click="changeOrderType(listData,'cancel')">{{$L('取消预约')}}</el-button>
            <el-button type="default" size="small" v-else-if="listData.order_status >= 3"  @click="changeOrderType(listData,'del')">{{$L('删除订单')}}</el-button>
            <el-button type="primary" size="small" v-if="listData.order_status >= 3 && listData.commentStatus === 0"  @click="changeOrderType(listData,'evaluate')">{{$L('去评价')}}</el-button>
            <el-button type="primary" size="small" v-if="listData.order_status >= 3 && listData.commentStatus === 1"  @click="changeOrderType(listData,'evaluateInfo')">{{$L('查看评价')}}</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "reservationInfo",
        data () {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                listData:{},
                commentStatus:1
            }
        },
        created (){
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData (){
                if(this.$route.params.id !==''){
                    const _self = this;
                    _self.loading = true;
                    let id = _self.$route.params.id;
                    _self.$api('bookOrderInfo', {bookId:id} , function (reData) {
                        if (reData.flag === 1) {
                            _self.listData = reData.data;
                            console.log( _self.listData );
                            if(_self.listData.order_status >= 3 && _self.listData.commentStatus === 1){
                                _self.commentStatus = 4;
                            } else if(_self.listData.order_status >= 3 && _self.listData.commentStatus === 0){
                                _self.commentStatus = 3;
                            } else {
                                _self.commentStatus = 1;
                            }
                            // _self.commentStatus = reData.data.commentStatus;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'reservation',params: {type: 'all'}});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'reservation',params: {type: 'all'}});
                        });
                    });
                }

            },
            statusFunc(value){
                let x;
                switch(value){
                    case 0:
                        x = {text:'待付款',color:'font-red'};
                        break;
                    default:
                        x = {text:'已付款',color:'font-green'};
                        break;
                }
                return x;
            },
            changeOrderType(item, status) {
                switch (status) {
                    case 'pay':
                        //立即付款
                        if(item.order_status === 0 || item.pay_status === 1){
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: orderPay,
                                width: '900px',
                                loading: true
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.order_status === 1) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('cancleReservation1', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'del':
                        //删除订单
                        if (item.order_status >= 3) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('delReservation', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.order_status >= 3 && item.commentStatus === 0) {
                            this.$router.push({name: "resOrderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //去评价
                        if (item.order_status >= 3 && item.commentStatus === 1) {
                            this.$router.push({name: "resCheckComment", params: {id: item.orderId}});
                        }
                        break;
                }
            },
            showLoad(type) {
                if (typeof type === "undefined" || type) {
                    this.loading = this.$loading({lock: true});
                } else {
                    if (this.loading) {
                        this.loading.close();
                        this.loading = null;
                    }
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/css/common/lib.scss';
    @import "../../../assets/css/common/common";
     .reservation-info{
         position: absolute;
         width: 100%;
         height: 100%;
         top: 0;
         left: 0;
         border-bottom: 1px solid #E4E7ED;
         .content-top{
             width: 100%;
             height: 120px;
             background: url("../../../assets/img/order/top-bg1.png") no-repeat center;
             .tips{
                 line-height: 120px;
                 padding-left: 20%;
                 color: #fff;
                 font-size: 16px;
             }
         }
         .order-details {
             margin: 10px 0 10px;
             padding: 10px;
             background: #fff;
             .title {
                 font-weight: 700;
                 margin-bottom: 20px;
                 font-size: 14px;
             }
             .order-ul {
                 width: 100%;
                 .order-li {
                     padding-bottom: 20px;
                 }
             }

         }
         .prod-info{
             margin-top: 10px;
             background: #fff;
             padding: 10px;
             width: 100%;
             .info{
                 width: 70%;
                 display: inline-block;
                 height: 80px;
                 .list-main-left{
                     height: 80px;
                     float: left;
                     .img{
                         width: 80px;
                         height: 80px;
                     }
                 }
                 .list-main-right{
                     float: left;
                     padding: 5px 0 0 10px;
                     width: calc(100% - 90px);
                     .name{
                         cursor: pointer;
                         overflow:hidden;
                         display:-webkit-box;
                         text-overflow:ellipsis;
                         -webkit-line-clamp:2;
                         -webkit-box-orient:vertical;
                         white-space:normal;
                         font-size: 14px;
                     }
                 }

             }
             .price{
                 width: 30%;
                 padding-top: 5px;
                 display: inline-block;
                 overflow: hidden;
                 white-space: nowrap;
                 vertical-align: top;
                 text-align: right;
             }
         }
         .prod-bottom{
             background: #fff;
             .right-box{
                 .ul{
                     padding: 0 10px;
                     .li{
                         line-height: 2;
                         p:first-child{
                             width: 50%;
                             display: inline-block;
                         }
                         p:last-child{
                             width: 50%;
                             display: inline-block;
                             text-align: right;
                         }
                         &:last-child{
                             p:last-child{
                                 color: #FF0000;
                                 font-size: 14px;
                             }
                         }
                     }
                 }

             }
         }
         .prod-msg{
             padding: 10px;
             border-top: 1px solid #eee;
             background: #fff;
             .order-sn{
                 line-height: 2;
             }
         }
         .bottom-btns{
             padding: 20px;
             background: #fff;
             border-top: 1px solid #eee;
             text-align: right;
             button{
                 width: 85px;
             }
         }
         .error {
             position: absolute;
             z-index: 200;
             top: 0;
             left: 0;
             width: 100%;
             height: 100%;
             background: #FFFFFF;
             overflow: hidden;

             .error-tips {
                 position: absolute;
                 top: 50%;
                 left: 50%;
                 width: 320px;
                 transform: translate(-50%, -50%);
             }
         }
     }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>
